<template>
  <v-container fluid>
    <!--卡片-->
    <v-row justify="center">
      <v-col sm="6" md="4" lg="4">
        <v-card class="mx-auto" elevation="10" color="rgba(255,255,255,0.5)">
          <v-img
            class="white--text"
            height="200px"
            src="../../assets/user/top.gif"
          >
            <v-app-bar flat color="rgba(0, 0, 0, 0)">
              <v-app-bar-nav-icon color="black"></v-app-bar-nav-icon>

              <v-toolbar-title class="text-h5 black--text pl-0">
                Login
              </v-toolbar-title>
            </v-app-bar>
          </v-img>
          <!--表单填写部分-->
          <v-card-text>
            <v-form
              ref="loginForm"
              v-model="valid"
              lazy-validation
              autocomplete="off"
            >
              <!--用户名-->
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="form.username"
                    :rules="rules.usernameRules"
                    label="用户名"
                    >{{ userNameR }}</v-text-field
                  >
                </v-col>
              </v-row>
              <!--密码-->
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="form.password"
                    :rules="rules.passwordRules"
                    label="密码"
                    :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                    @click:append="show = !show"
                    :type="show ? 'text' : 'password'"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>
          <!--动作按钮-->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn :disabled="!valid" depressed elevation="8" @click="login()"
              >Login</v-btn
            >
          </v-card-actions>
          <br />
        </v-card>
      </v-col>
    </v-row>
    <!--错误提示-->
    <v-snackbar v-model="snackbar">
      {{ snackbarData }}
      <template v-slot:action="{ attrs }">
        <v-btn color="pink" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
export default {
  name: "Login",
  data: () => ({
    // 用于验证表单内容填写是否正确
    valid: true,
    // 用于控制密码是否以可见的形式显示
    show: false,
    // 用于控制消息条是否显示
    snackbar: false,
    //控制条消息内容
    snackbarData: "",
    // 表单填写的内容
    form: {
      username: "",
      password: "",
    },
    // 验证规则
    rules: {
      usernameRules: [
        (v) => !!v || "用户名不能为空",
        (v) =>
          ((v || "").length >= 3 && (v || "").length <= 20) ||
          "用户名必须在3-20个字符以内",
      ],
      passwordRules: [
        (v) => !!v || "密码不能为空",
        (v) =>
          ((v || "").length >= 6 && (v || "").length <= 20) ||
          "密码必须在6-20个字符以内",
      ],
    },
  }),
  computed: {
    //获取来自store的username，即注册时的输入
    userNameR() {
      return this.$store.state.user.username;
    },
  },

  methods: {
    // 登录功能
    login() {
      // 先验证表单

      // 表单填写无误，向login接口发送post请求
      this.axios
        .post(this.targetIP() + "/verify.login", {
          username: this.form.username,
          password: this.form.password,
        })
        .then((response) => {
          // 验证成功，获取用户信息
          if (response.data == "wrongPassword") {
            this.snackbarData = "密码错误";
            this.snackbar = true;
          } else if (response.data == "nonExisted") {
            this.snackbarData = "该用户名不存在";
            this.snackbar = true;
            this.form.username = "";
          } else if (response.data == "methoderror") {
            this.snackbarData = "请求方法错误";
            this.snackbar = true;
          } else {
            //判断用户是否可用
            //let user=JSON.parse(response.data);
            //用了json（）方法封装arr
            console.log(response.data);

            if (response.data.enabled == "1") {
              // 设置全局user
              this.$store.commit("setUser_l", response.data);
              // 回到主页
              this.$router.push({
                name: "Home",
              });
            } else if (response.data.enabled == "2") {
              this.$store.commit("setUser_l", response.data);
              // 回到主页
              this.$router.push({
                name: "Home",
              });
            } else {
              this.snackbarData = "您的用户处于不可用状态";
              this.snackbar = true;
            }
          }
        })
        .catch((error) => {
          console.log(error);
          // 用户认证错误，显示消息条
          this.snackbar = true;
        });
    },
    targetIP() {
      return this.$store.state.targetIP;
    },
  },
};
</script>
